import { useState, useEffect, useCallback } from 'react';
import { GameState, Question, Reward } from '../types';

const INITIAL_TIME = 30;
const BASE_POINTS = 100;
const REWARD_CHANCE = 0.2;

const rewards: Reward[] = [
  { type: 'free_day', description: '1 ngày xem phim miễn phí', value: 1 },
  { type: 'discount', description: 'Giảm giá 10% gói đăng ký tháng', value: 10 },
  { type: 'points', description: '50 điểm thưởng', value: 50 },
];

export const useQuizGame = (questions: Question[]) => {
  const [gameState, setGameState] = useState<GameState>({
    currentQuestion: 0,
    score: 0,
    timeRemaining: INITIAL_TIME,
    answers: [],
    isGameOver: false,
  });

  const calculateTimeBonus = (timeLeft: number): number => {
    if (timeLeft > 20) return 50;
    if (timeLeft > 10) return 30;
    return 10;
  };

  const getRandomReward = (): Reward | null => {
    if (Math.random() < REWARD_CHANCE) {
      return rewards[Math.floor(Math.random() * rewards.length)];
    }
    return null;
  };

  const answerQuestion = useCallback((answerIndex: number) => {
    const currentQ = questions[gameState.currentQuestion];
    const isCorrect = answerIndex === currentQ.correctAnswer;
    const timeBonus = calculateTimeBonus(gameState.timeRemaining);
    
    setGameState(prev => ({
      ...prev,
      score: isCorrect ? prev.score + BASE_POINTS + timeBonus : prev.score,
      answers: [...prev.answers, answerIndex],
      currentQuestion: prev.currentQuestion + 1,
      timeRemaining: INITIAL_TIME,
      isGameOver: prev.currentQuestion + 1 >= questions.length,
    }));
  }, [gameState, questions]);

  useEffect(() => {
    if (gameState.isGameOver || gameState.timeRemaining <= 0) return;

    const timer = setInterval(() => {
      setGameState(prev => ({
        ...prev,
        timeRemaining: prev.timeRemaining - 1,
        isGameOver: prev.timeRemaining <= 1 || prev.currentQuestion >= questions.length,
      }));
    }, 1000);

    return () => clearInterval(timer);
  }, [gameState.isGameOver, gameState.timeRemaining, questions.length]);

  return {
    gameState,
    answerQuestion,
    getRandomReward,
  };
};
