import React, { useEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import confetti from 'canvas-confetti';
import { useGameSound } from '../hooks/useGameSound';

// Animation keyframes
const fadeIn = keyframes`
  from { opacity: 0; }
  to { opacity: 1; }
`;

const slideUp = keyframes`
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
`;

// Styled components
const MessageOverlay = styled.div`
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.8);
  padding: 1rem 2rem;
  border-radius: 8px;
  z-index: 1000;
  animation: ${fadeIn} 0.3s ease-out, ${slideUp} 0.3s ease-out;
  pointer-events: none;
`;

const Message = styled.h2`
  font-size: 1.5rem;
  color: white;
  margin: 0;
  text-align: center;
`;

interface Props {
  type: 'start' | 'correct' | 'wrong' | 'gameOver' | 'congratulations' | 'reward';
  onComplete?: () => void;
}

export const GameEffects: React.FC<Props> = ({ type, onComplete }) => {
  const { playGameStart, playCorrect, playWrong, playGameOver, playCongratulations, playReward } = useGameSound();

  useEffect(() => {
    // Play sound based on effect type
    switch (type) {
      case 'start':
        playGameStart();
        break;
      case 'correct':
        playCorrect();
        break;
      case 'wrong':
        playWrong();
        break;
      case 'gameOver':
        playGameOver();
        break;
      case 'congratulations':
        playCongratulations();
        break;
      case 'reward':
        playReward();
        // Show confetti for rewards
        confetti({
          particleCount: 100,
          spread: 70,
          origin: { y: 0.6 }
        });
        break;
    }

    // Auto-hide effect after delay
    const timer = setTimeout(() => {
      onComplete?.();
    }, 1500);

    return () => clearTimeout(timer);
  }, [type, onComplete, playGameStart, playCorrect, playWrong, playGameOver, playCongratulations, playReward]);

  const messages = {
    start: 'Bắt đầu!',
    correct: 'Chính xác! +100',
    wrong: 'Sai rồi!',
    gameOver: 'Game Over',
    congratulations: 'Chúc mừng!',
    reward: 'Bạn đã nhận được phần thưởng! 🎉'
  };

  return (
    <MessageOverlay>
      <Message>{messages[type]}</Message>
    </MessageOverlay>
  );
};
